<style lang="less" scoped>
  body {
    padding: 0;
    margin: 0;
    .sider-menu {
      float: right;
      display: none;
    }
    .top-menu {
      display: flex;
      justify-content: space-between;
    }
    .top-nav-items {
      ul {
        display: flex;
        justify-content: space-around;
        li {
          list-style: none;
          transition: .3s;
          color: skyblue;
          *:last-child {
            margin-top: 2px;
            line-height: 60px;
            padding: 0 15px;
          }
          a {
            text-align: center;
            text-decoration: none;
            line-height: 60px;
            padding: 0 15px;
            color: ghostwhite;
            transition: .5s;
            cursor: pointer;
            position: relative;
          }
          a:before {
            content: "";
            position: absolute;
            bottom: 0;
            left: 16px;
            width: 8px;
            height: 8px;
            border: 2px solid #e91e63;
            border-width: 0 0 2px 2px;
            transition: .5s;
            opacity: 0;
          }
          a:after {
            content: "";
            position: absolute;
            top: 0;
            right: 16px;
            width: 8px;
            height: 8px;
            border: 2px solid #e91e63;
            border-width: 2px 2px 0 0;
            transition: .5s;
            opacity: 0;
          }
          a:hover:before {
            bottom: -4px;
            left: -4px;
            opacity: 1;
          }
          a:hover:after {
            top: -4px;
            right: -4px;
            opacity: 1;
          }
          a:hover {
            color: #fff;
            background: #e91e63;
          }
        }
      }
    }
    .logo {
      line-height: 60px;
      text-transform: uppercase;
      color: ghostwhite;
      transition: .3s;
      cursor: pointer;
    }
    header {
      height: 60px;
      transition: .3s;
      backdrop-filter: blur(4px);
      .sider-menu #toggler {
        display: none;
        &:checked + label .hamburger-container span:nth-child(1) {
          transform: rotate(45deg);
          top: 5px;
          transition: top 0.3s ease-in-out, transform 0.3s ease-in-out 0.3s;
        }
        &:checked + label .hamburger-container span:nth-child(2) {
