          top: -4px;
          transition: top 0.3s ease-in-out, transform 0.3s ease-in-out 0.3s;
        }
        &:checked ~ .nav-items {
          transform: scaleY(1);
          opacity: 1;
        }
      }
      .sider-menu .hamburger-container {
        transition: .3s;
        display: block;
        width: 26px;
        height: 26px;
        position: relative;
        top: 24px;
        span {
