          height: 1px;
          background-color: skyblue;
          font-weight: bolder;
          position: relative;
          /*   transition:all .3s ease-in-out; */
          transition: transform 0.3s ease-in-out, top 0.3s ease-in-out 0.3s;
          top: 0;
          &:nth-child(2) {
            margin-top: 8px;
          }
        }
      }
    }
    .nav-items {
      width: 100%;
      height: 100vh;
      position: absolute;
      background: #ffffff;
      top: 44px;
      left: 0;
      transform: scaleY(0);
      transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
      transform-origin: 50% 0;
      opacity: 0;
      margin-left: 0!important;
      ul {
        margin-top: 1em;
        padding-left: 0!important;
        li {
          list-style: none;
          text-align: center;
          padding: 15px 0;
          a {
            font-size: 20px;
            color: #000000;
          }
        }
      }
    }
  }
  .shadow {
    position: fixed;
    z-index: 999999;
    width: 100%;
    top: 0;
  }
  header.sticky {
    background: #fff;
    height: 44px;
    .top-nav-items ul li a,
