<template>
  <div>
    <NarBar/>
    <div class="banner"></div>
    <div class="containers mr-tt">
      <a-row class="items" :gutter="24">
        <a-col :span="16">
          <a-card class="bread card">
            <a-breadcrumb>
              <a-breadcrumb-item><a href="/">首页</a></a-breadcrumb-item>
              <a-breadcrumb-item>标签</a-breadcrumb-item>
              <a-breadcrumb-item>{{queryParam.tag}}</a-breadcrumb-item>
            </a-breadcrumb>
          </a-card>
          <div class="article" v-for="item in articles" :key="item.id">
            <a-card v-if="item.title !== 'me'" hoverable class="card">
              <div class="imag" style="float: left">
                <img @click="watch(item.id)" :src="item.thumbnail?item.thumbnail:'https://cdn.jsdelivr.net/gh/Bursteretion/file-repo/imgs/wallhaven-2el5zg.jpg'" alt="thumbnail" width="272" height="150">
              </div>
              <div class="meta" style="float: right; width: 350px">
                <router-link class="link-black" :to="'/article/' + item.id">{{item.title}}</router-link>
                <div class="mr-t">
          <span>
            <a-icon type="calendar" />
            {{item.publish_time.substr(0,10)}}
          </span>
                  <span class="mr-l">
            <a-icon type="user" />
            {{item.author}}
          </span>
                </div>
                <div class="mr-t">
                  <router-link class="link-grey" :to="'/article/' + item.id">{{item.summary}}</router-link>
                </div>
                <div class="mr-t">
                  <a-tag v-for="i in item.categories.split(',')" color="pink" :key="i.id"><router-link :to="'/categories/' + i">{{i}}</router-link></a-tag>
                </div>
                <div class="mr-t">
                  <a-button @click="watch(item.id)" type="primary" ghost size="small">阅读更多</a-button>
                </div>
              </div>
            </a-card>
          </div>
          <div class="mr-tt" style="text-align: center">
            <a-pagination @change="onchange" :pageSize="pagination.pageSize" :default-current="1" :total="pagination.total" />
          </div>
        </a-col>
        <a-col class="right-items" :span="8">
          <Profile/>
          <Category/>
        </a-col>
      </a-row>
    </div>
    <Footer/>
    <BackToTop/>
  </div>
</template>

<script>
  import NarBar from "./components/NarBar";
  import Profile from "./components/Profile";
  import Category from "./components/Category";
  import ArticleApi from "../../api/Articles";
  import Footer from "./components/Footer";
  import BackToTop from "./components/BackToTop";

  export default {
    name: "Tags",
    components: {
      BackToTop,
      Footer,
      NarBar,
      Category,
      Profile,
    },
    data() {
      return {
        pagination: {
          pageSize: 8,
          total: 0
        },
        articles: [],
        queryParam: {
          page: 0,
          size: 7,
          keyword: '',
          tag: '',
          status: ''
        },
      }
    },
    created () {
      this.queryParam.tag = this.$route.params.name
      this.loadArticles()
    },
    methods: {
      async loadArticles() {
        ArticleApi.loadArticlesByTag(this.queryParam).then(res => {
          this.articles = res.data.articles
          this.pagination.total = res.data.articles.length
          window.document.title = '标签：' + this.queryParam.tag + ' - ETERNITY'
        })
      },
      onchange(page) {
        this.queryParam.page = page - 1
        this.loadArticles()
      },
      watch(id) {
        this.$router.push(`/article/${id}`)
      }
    }
  }
</script>

<style lang="less" scoped>
  .banner {
    position: relative;
    width: 100%;
    height: 500px;
    background: url("https://cdn.jsdelivr.net/gh/Bursteretion/file-repo/imgs/wallhaven-eyp9xl.png");
    background-size: 100% 100%;
  }
  .right-items > *:not(:first-child) {
    margin-top: 10px;
  }
  .article {
    margin-top: 10px;
  }
  .article:first-child {
    margin-top: 0!important;
  }
  .containers {
    padding-right: 15px;
    margin-bottom: 30px;
  }
  @media screen and (max-width: 800px) {
    .containers {
      width: 95%;
      padding-right: 0 !important;
    }
    .items > * {
      padding-left: 12px;
      padding-right: 12px;
      width: 100%;
    }
    .items > *:last-child {
      margin-top: 10px;
    }
  }
  @media screen and (max-width: 800px) {
    .card {
      .imag img {
        width: 100%;
      }
      margin-top: 10px;
    }
    .bread {
      margin-top: 25px;
    }
    .card:first-child {
      margin-top: 30px;
    }
    .meta {
      padding: 10px;
    }
    .read-btn {
      position: relative;
      bottom: 10px;
    }
    .banner {
      height: 300px;
    }
  }
</style>